import React, { useContext } from 'react';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Button from '../../Shared/Components/FormElements/Button';
import Card from '../../Shared/Components/UIElements/Card';
import ErrorModal from '../../Shared/Components/UIElements/ErrorModal';
import Map from '../../Shared/Components/UIElements/Map';
import Modal from '../../Shared/Components/UIElements/Modal';
import Spinner from '../../Shared/Components/UIElements/Spinner';
import AuthContext from '../../Shared/context/auth-context';
import { useHttpClient } from '../../Shared/hooks/http-hook';

import './PlaceItem.css';

function PlaceItem(props) {
    const authContext = useContext(AuthContext);
    const {cancelError,errorState, sendRequest, loading} = useHttpClient();
    const navigate = useNavigate()
    const [showMap, setShowMap] = useState(false);
    const [showDeleteWarning, setShowDeleteWarning] = useState(false);

    const openMapHandler = () => {
        setShowMap(true);
    }

    const closeMapHandler = () => {
        setShowMap(false)
    }

    const showDeleteWarningHandler = () => {
        setShowDeleteWarning(true)
    }
    const hideDeleteWarningHandler = () => {
        setShowDeleteWarning(false)
    }
    const deletePlaceHandler = async () => {
        try {
            await sendRequest(process.env.REACT_APP_BACKEND_URL + '/places/' + props.id, 'DELETE',null, {'Authorization': 'Bearer ' + authContext.token });
            setShowDeleteWarning(false);
            props.deletePlace(props.id); 
        } catch (error) {
            setShowDeleteWarning(false)
        }
    }
    
    return (
        <>
            <Modal show={showMap} onCancel={closeMapHandler} header={props.address} contentClass='place-item__modal-content' footerClass='place-item__modal-actions' footer={<Button onClick={closeMapHandler}>CLOSE</Button>}>
                <div className='map-container'>
                    <Map zoom={16} center={props.coordinates} />
                </div>
            </Modal>

            <Modal show={showDeleteWarning} onCancel={hideDeleteWarningHandler} header={'Are you sure you want to proceed?'} contentClass='place-item__modal-content' footerClass='place-item__modal-actions' footer={<><Button onClick={deletePlaceHandler}>DELETE</Button>
                <Button danger onClick={hideDeleteWarningHandler}>CLOSE</Button></>}>

            </Modal>

            <ErrorModal error={errorState} onClear={cancelError} />

            {loading && <Spinner asOverlay/>}



            <li className='place-item'>
                <Card className='place-item__content'>
                    <div className="place-item__image">
                        <img src={process.env.REACT_APP_ASSET_URL + '/' + props.image} alt={props.title} />
                    </div>
                    <div className="place-item__info">
                        <h2>{props.title}</h2>
                        <h3>{props.address}</h3>
                        <p>{props.description}</p>
                    </div>
                    <div className="place-item__actions">
                        <Button onClick={openMapHandler} inverse>VIEW ON MAP</Button>
                        {authContext.isLoggedIn && props.creator === authContext.userId && (
                            <>
                                <Button to={`/places/${props.id}`}>EDIT</Button>
                                <Button danger onClick={showDeleteWarningHandler}>DELETE</Button>
                            </>
                        )}

                    </div>
                </Card>

            </li>
        </>
    );
}

export default PlaceItem;