import React from 'react';
import PlaceItem from './PlaceItem';
import Card from './../../Shared/Components/UIElements/Card';
import Button from '../../Shared/Components/FormElements/Button';

import './PlaceList.css';

function PlaceList(props) {
    if (props.places.length === 0) {
        return (
            <div className='place-list center'>
                <Card>
                    <h2>No places found, maybe create one ?</h2>
                    <Button>Share Place</Button>
                </Card>
            </div>)
    }
    const placeItems = props.places.map((place) => {
        return <PlaceItem deletePlace={props.deletePlace} key={place.id} id={place.id} image={place.image} title={place.title} description={place.description} address={place.address} creator={place.creator} coordinates={place.location} />
    })
    return (
        <ul className='place-list'>
            {placeItems}
        </ul>
    );
}

export default PlaceList;