import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import ErrorModal from '../../Shared/Components/UIElements/ErrorModal';
import Spinner from '../../Shared/Components/UIElements/Spinner';
import { useHttpClient } from '../../Shared/hooks/http-hook';
import PlaceList from '../Components/PlaceList';


function UserPlaces(props) {
    const { sendRequest, errorState, cancelError, loading } = useHttpClient();
    const [userPlaces, setUserPlaces] = useState([])

    const { userId } = useParams();
    useEffect(() => {
        const fetchUserPlaces = async () => {
            try {
                const data = await sendRequest(process.env.REACT_APP_BACKEND_URL+'/places/user/' + userId, 'GET');
                setUserPlaces(data.places)
            } catch (error) {
                setUserPlaces([]);
            }
        }
        fetchUserPlaces();
    }, [sendRequest, userId])

    const deletePlaceHandler = (placeId) => {
        setUserPlaces(userPlaces => userPlaces.filter(place => place.id !== placeId))
    }   


    return (
        <>
            {loading && <Spinner asOverlay />}
            <ErrorModal error={errorState} onClear={cancelError} />
            <PlaceList places={userPlaces} deletePlace={deletePlaceHandler} />
        </>
    );
}

export default UserPlaces;